<template>
  <div>
    <layout-box one="商铺活动">
      <el-button type="primary" size="small" @click="$router.push('/mark/activity/add')">新增活动</el-button>
      <el-table style="width: 100%" size="mini" :data="tableData">
        <el-table-column prop="name" label="活动名称" />
      </el-table>
      <!-- 分页 -->
      <el-pagination
        class="mt-1 text-right"
        background
        :current-page="queryInit.pageNo"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="queryInit.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryInit.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </layout-box>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'markActivity',

  components: {
    LayoutBox,
  },

  data () {
    return {
      tableData: [],
      queryInit: { // 分页
        pageNo: 1,
        pageSize: 10,
        total: 0
      },
    }
  },

  created () {
    this.apiGetActivityList()
  },

  methods: {
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
    },
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
    },

    // --------------------------------------
    // 查询平台营销活动列表
    async apiGetActivityList () {
      const res = await this.$http.get(`${this.$api.getActivityList}?shopId=${localStorage.getItem('shopId')}`)
      this.tableData = res.data.data
      console.log(res)
    },
  }
}
</script>

